@font-face {
  font-family: "Gotham Bold";
  src: url("/assets/fonts/Gotham-Bold.woff") format("woff"),
    url("/assets/fonts/Gotham-Bold.ttf") format("truetype");
}

@font-face {
  font-family: "Gotham Rounded Light";
  src: url("/assets/fonts/GothamRounded-Light.woff") format("woff"),
    url("/assets/fonts/GothamRounded-Light.ttf") format("truetype");
}

@font-face {
  font-family: "Helvetica Neue Light";
  src: url("/assets/fonts/HelveticaNeueLight.woff") format("woff"),
    url("/assets/fonts/HelveticaNeueLight.ttf") format("truetype");
}

@font-face {
  font-family: "Helvetica Neue Medium";
  src: url("/assets/fonts/HelveticaNeueMedium.woff") format("woff"),
    url("/assets/fonts/HelveticaNeueMedium.ttf") format("truetype");
}

@font-face {
  font-family: "Helvetica Neue Bold";
  src: url("/assets/fonts/HelveticaNeueBold.woff") format("woff"),
    url("/assets/fonts/HelveticaNeueBold.ttf") format("truetype");
}

// Colors

$color-primary: #d35266;
$color-secundary: #888;
$color-tertiary: #383838;

// Fonts

$gotham-bold: "Gotham Bold";
$gotham-rounded: "Gotham Rounded Light";
$helvetica-light: "Helvetica Neue Light";
$helvetica-medium: "Helvetica Neue Medium";
$helvetica-bold: "Helvetica Neue Bold";

// Font Sizes

$fontsize-m: 1.5rem;
$fontsize-l: 3.5rem;

// Globals

html {
  font-size: 62.5%; //REM Reset
}

// -------

// Navigation

.nav-container {
  background-color: $color-tertiary;
}

.header-nav {
  .logo {
    color: $color-primary;
    font-size: $fontsize-l;
  }

  ul {
    li {
      font-size: $fontsize-m;
    }
  }


}

.hero-container {
  background-color: $color-primary;
}

h1 {
  font-size: 10rem;
  font-family: $gotham-bold;
  color: $color-primary;
}

h2 {
  font-family: $gotham-rounded;
  color: $color-secundary;
}